import axios from "axios";

/// класс обеспечивающий получение данных на frontend
class ApiProvider {
    static apiUrl = '/ajax.php';

    static getMapPoints = async () => {
        let points = [];
        const response = await axios.post(this.apiUrl, {
            method: 'map_points'
        })
        if (response.status === 200 && response.data['points'].length > 0) {
            points = response.data['points'];
        }
        return points;
    }

    static getVacancies = async () => {
        let vacancies = [];
        const response = await axios.post(this.apiUrl, {
            method: 'vacancies'
        })
        if (response.status === 200 && response.data['vacancies'].length > 0) {
            vacancies = response.data['vacancies'];
        }
        return vacancies;
    }

    static getBanners = async () => {
        let banners = [];
        const response = await axios.post(this.apiUrl, {
            method: 'banners'
        })
        if (response.status === 200 && response.data['banners'].length > 0) {
            banners = response.data['banners'];
        }
        return banners;
    }

    static getInstagramFeed = async (pageSize = 0, page = 0) => {
        let result = {photos: [], total: 0};
        const response = await axios.post(this.apiUrl, {
            method: 'instagram_feed',
            page: page,
            pageSize: pageSize,
        })
        if (response.status === 200 && response.data['photos'].length > 0) {
            result.photos = response.data['photos'];
            result.total = response.data['total'];
        }
        return result;
    }
}

export default ApiProvider;