import React from 'react';
import ApiProvider from "../providers/api_provider";
import {Placemark} from "react-yandex-maps";

class InstagramFeed extends React.Component {
    state = {
        page: 0,
        pageLoading: true,
        totalCnt: 0,
        photos: [],
    }

    loadData = async (pageNum = 0) => {
        this.setState({
            pageLoading: true,
            page: pageNum,
        });

        const vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0)
        let pageSize = 5;
        if (vw >= 1440 && pageNum !== 0) {
            pageSize = 8;
        } else if (vw < 1440 && pageNum !== 0) {
            pageSize = 6;
        }
        const photosGet = await ApiProvider.getInstagramFeed(pageSize, pageNum);

        this.setState({
            pageLoading: false,
            photos: [...this.state.photos, ...photosGet.photos],
            totalCnt: photosGet.total,
        });
    }

    componentDidMount() {
        this.loadData();
    }

    render() {
        if (!this.state.pageLoading && this.state.totalCnt === 0) return;
        return (
            <div className="instagram-feed__wrapper">
                <div className="wrapper">
                    <h2 className="instagram-feed__title h2">мы в инстаграме</h2>
                    <div className="instagram-feed__items">{this.renderItems()}</div>
                    {this.state.pageLoading && this.renderLoader()}
                    {(this.state.totalCnt > this.state.photos.length && !this.state.pageLoading) && this.renderLoadMore()}
                </div>
            </div>
        )
    }

    renderItems = () => {
        const items = [];
        for (let i = 0; i < this.state.photos.length; i++) {
            items.push(<div className="instagram-feed__items__photo"/>);
        }
        return (
            <React.Fragment>{items}</React.Fragment>
        )
    }

    renderLoader = () => {
        const vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0)
        const loaders = [];
        if (vw >= 1440) {
            for (let i = 0; i < (this.state.page > 0 ? 8 : 5); i++) {
                loaders.push(<div className="instagram-feed__items__loader"/>);
            }
        } else {
            for (let i = 0; i < (this.state.page > 0 ? 6 : 5); i++) {
                loaders.push(<div className="instagram-feed__items__loader"/>);
            }
        }
        return (
            <div
                className={`instagram-feed__items__loader-list${this.state.page === 0 ? ' first-page' : ' other-page'}`}>
                {loaders}
            </div>
        )
    }

    renderLoadMore = () => {
        return (
            <button onClick={() => {
                this.loadData(this.state.page + 1);
            }} className={'instagram-feed__items__load-more'}>показать ещё</button>
        )
    }
}

export default InstagramFeed;